<template>
  <div>
    <div class="q-pb-lg">
      <q-btn
        flat
        no-caps
        rounded
        :dense="dense"
        class="text-primary bg-primary-100 text-caption font-medium q-px-md cursor-pointer letter-space-normal hover-text-secondary"
        :href="url"
        @click.prevent="openURL(url)"
      >
        <q-icon
          :name="formatPlatformIconSrc('Loom')"
          size="xs"
          class="q-pr-sm"
        />
        Create with Loom
      </q-btn>
    </div>
    <q-input
      outlined
      :autofocus="autofocus"
      hide-bottom-space
      v-model="modelValue"
      :placeholder="placeholder"
      :name="name || undefined"
      class="rounded text-body2 input-white"
      :rules="[
        (val) =>
          checker.input(
            {
              value: val,
              nomsg: true,
              type: 'url',
            },
            500,
            required
          ),
      ]"
    />
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import { openURL } from 'quasar';
import { checker } from 'src/assets/scripts/functions';
import { mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'InputVideoWalkthrough',

  emits: ['updated'],

  props: {
    initModel: {},
    autofocus: {
      type: Boolean,
      default: false,
    },
    selection: {
      type: Array,
      default: () => [],
    },
    name: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: 'Your video walkthrough link',
    },
    required: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      checker,
      openURL,
      url: 'https://www.loom.com',
      modelValue: '',
    };
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.modelValue = val;
      },
    },
    modelValue: {
      async handler(val) {
        this.$emit('updated', val);
      },
    },
  },

  mounted() {
    if (this.initModel) this.modelValue = this.initModel;
  },

  methods: {
    ...mapActions(useGrowmodo, ['formatPlatformIconSrc']),
  },
});
</script>
